import react, { useState } from "react";
import "./styles.css";
const emoji = require("emoji-dictionary");
export default function App() {
  var [userInput, setuserInput] = useState("");
  var [result, setResult] = useState("Ask! first");
  function ipchangeHandler(event) {
    var inpEmoji = event.target.value;
    setuserInput(inpEmoji);
    var res = emoji.getName(inpEmoji);
    if (res === undefined) res = "Does not exist!";
    setResult(res);
  }
  return (
    <div className="App">
      <h1> We Gotcha your back</h1>
      <input onChange={ipchangeHandler}></input>
      <div>
        <br />
        meaning: {result}
      </div>
    </div>
  );
}
//VISER- View->Interact->State in Event Handler->Render new Value
